import React from 'react'

function List(props) {
    let counter = 0

    const todo = props.kaxee.map(work=>{
       counter++
        return(
            <tr>
                <td>{counter}</td>
                <td>{work.name}</td>
                <td>{work.IdNumber}</td>
                <td>{work.PhoneNumber}</td>
                <td>{work.Location}</td>
            </tr>
        )
    })
    return (
        <div className="todo">
            <h1>Todolists</h1>
            <table>
                <thead>
                    <tr>
                        <th>Sno</th>
                        <th>Name</th>
                        <th>IdNumber</th>
                        <th>Phonenumber</th>
                        <th>Location</th>

                    </tr>
                </thead>
                <tbody>
                    {todo}
                </tbody>
            </table>
        </div>
    )
}

export default List
