import React, { useState } from 'react'
import AddTodo from './AddTodo'
import List from './List';

function Todo() {
    const [state, updateState] = useState({

        Todolists:[],

    })
    const Messenger = (keen)=>{
        updateState({
  
        Todolists: [...state.Todolists, keen]

  
        })
    }
    return (
        <div>
            <AddTodo farrin={Messenger}/>
            <List kaxee={state.Todolists} />
        </div>
    )
}

export default Todo
