import React, {useState} from 'react'

function AddTodo(props) {
    const [state, updateState] = useState({
        Name: '',
        IdNumber: '',
        PhoneNumber: '',
        Location: '',


    })
    const HandleChange = (e) => {
        updateState({
            ...state, [e.target.name]: e.target.value,
            // Name:'ahmed'

        })

    }
    const HandleSubmit = (e) => {
        e.preventDefault()
        props.farrin(state)
        e.target.reset();

    }
    return (
        <div>
            <form onSubmit={HandleSubmit}>
                <input type="text" name='name' onChange={HandleChange} placeholder="ENter name" /><br /><br />
                <input type="text" name='IdNumber' onChange={HandleChange} placeholder="ENter IdNumber" /><br /><br />
                <input type="text" name='PhoneNumber' onChange={HandleChange} placeholder="ENter Phonenumber" /><br /><br />
                <input type="text" name='Location' onChange={HandleChange} placeholder="ENter Location" /><br /><br />
                <button type='submit'>Add Task</button>



            </form>
        </div>
    )
}

export default AddTodo
